/* eslint-disable jsx-a11y/anchor-is-valid */
import React from "react";
import { ListGroup, InputGroup, FormControl, Image } from "react-bootstrap";
import deletePNG from "./delete.png";
import "bootstrap/dist/css/bootstrap.min.css";

class ListOutline extends React.Component {
  constructor(props) {
    super(props);
    this.myRef = React.createRef();
    this.handleEditTask = this.handleEditTask.bind(this);
    this.handleDelete = this.handleDelete.bind(this);
  }
  handleEditTask(event) {
    const newTask = event.target.value;
    const id = event.target.id.slice(5);
    this.props.editTask(id, newTask);
  }
  handleDelete(event) {
    const id = event.target.id.slice(5);
    console.log(id);
    this.props.removeTask(id);
  }
  render() {
    return (
      <ListGroup
        variant="flush"
        className="m-auto pb-4 w-75 todo_list border-0"
      >
        {this.props.tasks.map((task, index) => {
          return (
            <ListGroup.Item className="p-0 rounded todo_item mt-3 border-0">
              <InputGroup>
                <FormControl
                  className="todo_item"
                  value={task}
                  ref={this.myRef}
                  key={index}
                  id={`list-${index}`}
                  onChange={this.handleEditTask}
                />
                <InputGroup.Append>
                  <InputGroup.Text id="basic-addon2" className="py-0 m-0">
                    <a
                      className="btn btn-delete float-right py-0"
                      as="button"
                      href="#"
                      id={`list-${index}`}
                      onClick={this.handleDelete}
                    >
                      <Image className="delete " src={deletePNG} />
                    </a>
                  </InputGroup.Text>
                </InputGroup.Append>
              </InputGroup>
            </ListGroup.Item>
          );
        })}
      </ListGroup>
    );
  }
}
export default ListOutline;
