import React from "react";
import ListOutline from "../ListGroup/ListOutline";

import "bootstrap/dist/css/bootstrap.min.css";
import { Container, Row, Col, Form, Button } from "react-bootstrap";

class App extends React.Component {
  constructor(props) {
    super(props);
    this.addTask = this.addTask.bind(this);
    this.editTask = this.editTask.bind(this);
    this.removeTask = this.removeTask.bind(this);
    this.state = {
      tasks: []
    };
  }
  addTask() {
    let tasks = this.state.tasks;
    let task = this.task.value;
    if (task !== "") {
      tasks.push(task);
      this.setState({ tasks: tasks });
      document.getElementById("Enter_task").value = "";
      console.log(this.state.tasks);
    } else {
      alert("Please type in words!");
    }
  }
  editTask(index, newTask) {
    let tasks = this.state.tasks;
    tasks[index] = newTask;
    this.setState({ tasks: tasks });
    console.log(this.state.tasks);
  }
  removeTask(index) {
    let tasks = this.state.tasks;
    tasks.splice(index, 1);
    this.setState({ tasks: tasks });
    console.log(this.state.tasks);
  }
  render() {
    return (
      <Container className="main bg-secondary w-100" fluid="true">
        <Row className="justify-content-center w-100 m-0">
          <Col xs={10} md={6} xl={4} className="bg-dark pt-5 rounded-top px-0">
            <h1 className="text-center text-white mb-5">-- To Do List --</h1>
            <Form className="form-inline justify-content-center m-auto">
              <Form.Group
                className="mx-sm-3 mb-2 border-0"
                controlId="Enter_task"
              >
                <Form.Control
                  ref={task => {
                    this.task = task;
                  }}
                  type="text"
                  placeholder="Enter task"
                  className="bg-info"
                />
              </Form.Group>
              <Button
                variant="primary"
                type="button"
                className="btn btn-warning mx-1 mx-sm-3 px-lg-5 mb-2 px-xl-5 py-1"
                onClick={this.addTask}
              >
                Add
              </Button>
            </Form>
          </Col>
        </Row>
        <Row className="justify-content-center w-100 m-0">
          <Col
            xs={10}
            md={6}
            xl={4}
            className="bg-dark py-3 rounded-bottom mb-5"
          >
            <ListOutline
              tasks={this.state.tasks}
              editTask={this.editTask}
              removeTask={this.removeTask}
            />
          </Col>
        </Row>
      </Container>
    );
  }
}

export default App;
